<template>
  <div id="home">
  </div>
</template>

<script>
// @ is an alias to /src
import * as THREE from "three";
import Orbitcontrols from "three-orbitcontrols";
import { OBJLoader, MTLLoader } from "three-obj-mtl-loader";
import GLTFLoader from "three-gltf-loader";
import { DirectionalLight } from "three";

let scene, camera, renderer;

export default {
  name: "home",
  data() {
    return {
      publicPath: process.env.BASE_URL
    };
  },
  components: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      let _this = this;
      scene = new THREE.Scene();
      scene.background = new THREE.Color(0xcccccc);

      camera = new THREE.PerspectiveCamera(
        45,
        window.innerWidth / window.innerHeight,
        1,
        5000
      );
      camera.rotation.y = (45 / 180) * Math.PI;
      camera.position.x = 800;
      camera.position.y = 100;
      camera.position.z = 1000;

      let hlight = new THREE.HemisphereLight(0xddeeff, 0x0f0e0d, 5);
      scene.add(hlight);

      let directionalLight = new THREE.DirectionalLight(0xffffff, 100);
      directionalLight.position.set(0, 1, 0);
      directionalLight.castShadow = true;
      scene.add(directionalLight);

      let light1 = new THREE.PointLight(0xc4c4c4, 30);
      light1.position.set(0, 300, 500);
      scene.add(light1);

      let light2 = new THREE.PointLight(0xc4c4c4, 30);
      light2.position.set(500, 100, 0);
      scene.add(light2);

      let light3 = new THREE.PointLight(0xc4c4c4, 30);
      light3.position.set(0, 100, -500);
      scene.add(light3);

      let light4 = new THREE.PointLight(0xc4c4c4, 30);
      light4.position.set(-500, 300, 0);
      scene.add(light4);

      renderer = new THREE.WebGLRenderer();
      renderer.setSize(window.innerWidth, window.innerHeight);
      document.getElementById("home").appendChild(renderer.domElement);

      // let mtlLoader = new MTLLoader();
      // mtlLoader.load(`${_this.publicPath}model/judy.mtl`, function(materials) {
      //   console.log(materials);
      //   materials.preload();
      //   let objLoader = new OBJLoader();
      //   objLoader.load(`${_this.publicPath}model/judy.obj`, function(obj) {
      //     scene.add(obj);
      //   });
      // });

      let gltfLoader = new GLTFLoader();
      gltfLoader.load(`${this.publicPath}model/car/scene.gltf`, function(gltf) {
        let car = gltf.scene.children[0];
        car.scale.set(0.4, 0.4, 0.4);
        scene.add(gltf.scene);
      });

      let controls = new Orbitcontrols(camera);
      // controls.addEventListener('change', renderer);

      var animate = function() {
        renderer.render(scene, camera);
        controls.update();
        requestAnimationFrame(animate);
      };
      animate();
    }
  }
};
</script>

